import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import { addOrder, closeAddOrderPopup } from '../../../redux/order/actions';
import { Header, AddressDetails, PackageDetails, OrderPreview } from '../components';

const steps = ['Shipper & Consignee Details', 'Package Details', 'Confirmation'];

export default function HorizontalLinearStepper() {
  const [senderDetails, setSenderDetails] = useState({});
  const [receiverDetails, setReceiverDetails] = useState({});
  const [itemList, setItemList] = useState([]);
  const [isDocument, setIsDocument] = useState(false);
  const [currency, setCurrency] = useState('');

  const [activeStep, setActiveStep] = React.useState(0);
  const [openDiscardModal, setOpenDiscardModal] = React.useState(false);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
  const userData = useSelector(state => state.auth.userData);
  const isAddLoading = useSelector(state => state.order.isAddLoading);
  const isAddSuccess = useSelector(state => state.order.isAddSuccess);
  const isAddFailed = useSelector(state => state.order.isAddFailed);

  /*
   Redirect to home if user is not logged in.
   */
  useEffect(() => {
    if (!isLoggedIn) {
      navigate("/");
  }
  },[]);

  /*
    Stepper Navigation.
   */
  const onClickStepper = (step) => {
    if (step <= activeStep) {
      setActiveStep(step);
    }
  }

  /*
   To move to Item after savings addresses
   */
  const onSaveAndContinueAddress = ({ senderDetails, receiverDetails }) => {
    setSenderDetails(senderDetails);
    setReceiverDetails(receiverDetails);
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }

  /*
   To move to preview after savings items details
   */
  const onSaveAndContinueItem = ({ itemList, isDocument }) => {
    setItemList(itemList);
    setIsDocument(isDocument);
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }

  /*
   To handle SAVE order
   */
  const handleSaveOrder = () => {
    const orderDetails = { senderDetails, receiverDetails, itemList, currency, isDocument, status:"SAVE" };
    dispatch(addOrder(userData.LoginID, userData.Email, userData.RoleNum, orderDetails));
  };

  /*
   To handle SUBMIT order
   */
  const handleSumbitOrder = () => {
    const orderDetails = { senderDetails, receiverDetails, itemList, currency, isDocument, status:"SUBMIT" };
    dispatch(addOrder(userData.LoginID, userData.Email, userData.RoleNum, orderDetails));
  }

  /*
   To close the popup if its an error.
   */
  const handlePopupClose = () => {
    if (isAddFailed) {
      dispatch(closeAddOrderPopup());
    }
  }

  /*
   To handle BACK action from header.
   */
  const onBackClick = () => {
    handleDiscardModalOpen();
  }
  const handleDiscardModalOpen = () => {
    setOpenDiscardModal(true);
  };
  const handleDiscardModalClose = () => {
    setOpenDiscardModal(false);
  };
  
  /*
   User to navigate to order list.
   */
  const goToOrderList = () => {
    navigate("/home/order");
  }

  /*
   User to reset new order page.
   */
  const createNewOrder = () => {
    setActiveStep(0);
    setSenderDetails({});
    setReceiverDetails({});
    setItemList([]);
  }

  return (
    <React.Fragment>
      { /* HEADER */}
      <Header title={"Create Order"} onRightBtnText={"Back"} onRightBtnClick={onBackClick} onRightBtnIcon={<ArrowBackIosIcon />} />

      { /* STEPPER WITH CONTENT */}
      <Paper sx={{ padding: '24px', maxHeight: 'calc(100vh - 180px)', overflowY: 'scroll' }}>
        <Stepper activeStep={activeStep} orientation="vertical">
            { steps.map((label, index) => {
              return (
                  <Step key={label}>
                      <StepLabel onClick={(e) => { onClickStepper(index) }}>{label}</StepLabel>
                      <StepContent>
                        { activeStep === 0 ? <AddressDetails sender={senderDetails} receiver={receiverDetails} 
                          saveAndContinueAddress={onSaveAndContinueAddress}/> : null }
                        { activeStep === 1 ? <PackageDetails items={itemList} saveAndContinueItem={onSaveAndContinueItem} isDocument={isDocument} /> : null }
                      </StepContent>
                  </Step>
              );
            })}
        </Stepper>

        { /* ORDER PREVIEW with ACTIONS */}
        {activeStep === (steps.length -1 ) && (
            <React.Fragment>
                <Typography sx={{ mt: 2, mb: 1 }}>
                    <OrderPreview senderDetails={senderDetails} receiverDetails={receiverDetails} itemDetails={itemList} isDocument={isDocument} 
                      selectedCurrency={currency} onCurrencyChange={(selectedCurrencyCode) => {
                        setCurrency(selectedCurrencyCode);
                      }}/>
                </Typography>
                <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                    <Box sx={{ flex: '1 1 auto' }} />
                    <Button variant="outlined" onClick={handleSaveOrder} style={{ marginRight: '20px'}}> Save </Button>
                    <Button variant="contained" onClick={handleSumbitOrder}> Submit </Button>
                </Box>
            </React.Fragment>
        )}

        { /* DISCARD CHANGES - Popup */}
        <Dialog
            open={openDiscardModal}
            onClose={handleDiscardModalClose}>

            <DialogTitle style={{ padding: '30px' }}>
                {"Discard Changes"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    Changes will not be saved. Do you want to proceed?
                </DialogContentText>
            </DialogContent>
            <DialogActions style={{ padding: '30px' }}>
                <Button onClick={handleDiscardModalClose}>Cancel</Button>
                <Button variant="contained" onClick={goToOrderList}>Discard</Button>
            </DialogActions>
        </Dialog>
      </Paper>


      { /* SUCCESS MODAL - Popup */}
      <Dialog
          open={isAddSuccess || isAddFailed}
          onClose={handlePopupClose}>
          <DialogTitle style={{ display: 'flex', justifyContent: 'center', padding: '30px' }}>
              { isAddSuccess ?  <CheckCircleIcon style={{ width: '64px', height: '64px', color: '#188b18' }}/>
                    : <ErrorOutlineIcon style={{ width: '64px', height: '64px', color: '#fd5353' }} />
              }
          </DialogTitle>
          <DialogContent style={{ display: 'flex', justifyContent: 'center' }}>
              <DialogContentText>
                  { isAddSuccess ? "Order created successfully." : "Error in creating new Order." }
              </DialogContentText>
          </DialogContent>
          {
            isAddSuccess &&
            <DialogActions style={{ padding: '30px' }}>
                <Button variant="contained" onClick={goToOrderList}>Go To Order List</Button>
                <Button variant="contained" onClick={createNewOrder} autoFocus>Create New Order</Button>
            </DialogActions>
          }
      </Dialog>

        { isAddLoading && 
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={true}
                onClick={() => {}}>
                <CircularProgress color="inherit" />
            </Backdrop>
        }
    </React.Fragment>
  );
}