import { AxiosInstance } from "./axios-instance";
import { URL } from './url';

const orderResponseTransformer = (response) => {
    // TODO: Response should have the info about whether its document or package.
    const data = JSON.parse(response.data);
    const resultCode = data.ResultCode;
    const responseResult = data.Results;

    // TODO: Transform this only if it success.
    let result = {sender: null, receiver: null, itemList: [], isDocument: false, currency: "" }
    result.sender = {
        name: responseResult.Send_Name,
        company: "Sender Company",
        proofId: responseResult.send_proof_id,
        addressLine1: responseResult.send_address,
        addressLine2: responseResult.send_city,
        state: responseResult.send_country,
        postalCode: responseResult.send_pincode,
        phone: responseResult.send_phone,
        email: responseResult.send_email,
    };
    result.receiver = {
        name: responseResult.rece_Name,
        company: "Receiver Company",
        proofId: responseResult.rece_proof_id,
        addressLine1: responseResult.rece_address,
        addressLine2: responseResult.rece_city,
        state: responseResult.rece_country,
        postalCode: responseResult.rece_pincode,
        phone: responseResult.rece_phone,
        email: responseResult.rece_email,
    };
    result.itemList = [...responseResult.Packs];
    result.currency = responseResult.Ord_currency;

    return {
        ResultCode: resultCode,
        Results: {...result}
    };
}

const getAllOrder = (loginID, token, roleNum) => {
    const requestBody = { LoginID: loginID, token, Number: roleNum };
    return AxiosInstance.post(URL.GET_ALL_ORDER, requestBody);
};

const  getOrder = (loginID, token, roleNum, invoiceId) => {
    const requestBody = { LoginID: loginID, token, Number: roleNum, invoice_value: 'TL34' };
    return AxiosInstance.post(URL.GET_ORDER, requestBody);
};

const addOrder = (requestBody) => {
    // console.log('requestBody in service:', requestBody);
    return AxiosInstance.post(URL.NEW_ORDER, requestBody);
};

const updateOrder = (loginID, token, roleNum, orderDetails) => {
    const requestBody = { ...orderDetails };
    return AxiosInstance.post(URL.UPDATE_ORDER, requestBody);
};

const cancelOrder = (loginID, token, roleNum, invoiceId) => {
    const requestBody = { LoginID: loginID, token, Number: roleNum, invoice_value: invoiceId };
    return AxiosInstance.post(URL.CANCEL_ORDER, requestBody);
};

export default { getAllOrder, getOrder, addOrder, updateOrder, cancelOrder}; 