import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import { Header, OrderTable, OrderPrint } from "./../components";
import { getOrder, clearSelectedOrder, cancelOrder } from '../../../redux/order/actions';
import './OrderList.scss';

const ACTION_TYPE = {
  PRINT : 'PRINT_ORDER',
  PREVIEW: 'VIEW_ORDER',
  CANCEL: 'CANCEL_ORDER'
}

const OrderList = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  
  const [openPreviewPopup, setOpenPreviewPopup]=useState(false);
  const [openCancelModal, setOpenCancelModal]=useState(false);
  const [isPrint, setIsPrint]=useState(false);
  const [selectedOrderId, setSelectedOrderId] = useState("");

  const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
  const isOrderListLoading = useSelector(state => state.order.isOrderListLoading);
  const isOrderLoading = useSelector(state => state.order.isOrderLoading);
  const userData = useSelector(state => state.auth.userData);
  const selectedOrder = useSelector(state => state.order.selectedOrder);

  /*
   Goto Login page if the user is not already logged in
   */
  useEffect(() => {
    if (!isLoggedIn) {
      navigate("/");
    }
  },[]);

  /*
   Navigate to new order page.
   */
  const onNewOrder = () => {
    navigate("/home/neworder");
  }

  /*
   To be invoked when clicking action(Preview, Print, Cancel) from table.
   */
  const onActionClick =  (actionType, invoiceId) => {
    // TODO: Use different component for preview
    console.log('Action:', actionType, invoiceId);
    setSelectedOrderId(invoiceId);
    if (ACTION_TYPE.CANCEL === actionType) {
      onCancelOrder();
    } else {
      setIsPrint(actionType === ACTION_TYPE.PRINT);
      onGetSelectedOrder();
    }
  }

  /*
   To get selected order details from API, thru action.
   */
  const onGetSelectedOrder = () => {
    dispatch(getOrder(userData.LoginID, userData.Email, userData.RoleNum, selectedOrderId));
  }

  /*
   Print the order details after resolving selected order.
   */
  useEffect(() => {
    // THIS IS FOR PRINT.
    if (selectedOrder) {
      setOpenPreviewPopup(true);
      setTimeout(() => {
        if (isPrint) {
          window.print();
          setOpenPreviewPopup(false);
          dispatch(clearSelectedOrder())
        }
      }, 1);
    }
  },[selectedOrder]);

  /*
   To close preview order while clicking backdrop.
   */
  const onClosePreview = () => {
    console.log('onClosePrintPreview called');
    setOpenPreviewPopup(false);
    dispatch(clearSelectedOrder())
  }

  /*
   To Open confirm cancel popup.
   */
  const onCancelOrder = () => {
    setOpenCancelModal(true);
  }

  /*
   To continue the cancel order.
   */
  const onContinueCancelOrder = () => {
    dispatch(cancelOrder(userData.LoginID, userData.Email, userData.RoleNum, selectedOrderId));
    setOpenCancelModal(false);
    // TODO: Need to refresh the table after order cancelled.
  }

  return (
    <React.Fragment>
      <Header title={"Order List"} onRightBtnText={"New Order"} onRightBtnIcon={<AddIcon />} onRightBtnClick={onNewOrder} />
      <OrderTable onActionClick={onActionClick}/>
      { openPreviewPopup && <OrderPrint selectedOrder={selectedOrder} onClose={onClosePreview}/> }

      { /* CancelOrder - Confirm Popup */}
      <Dialog
          open={openCancelModal}>

          <DialogTitle style={{ padding: '30px' }}>
              {"Cancel Order"}
          </DialogTitle>
          <DialogContent>
              <DialogContentText id="alert-dialog-description">
                  Are you sure, you want to cancel the order?
              </DialogContentText>
          </DialogContent>
          <DialogActions style={{ padding: '30px' }}>
              <Button onClick={() => { setOpenCancelModal(false)}} autoFocus>Close</Button>
              <Button variant="contained" onClick={onContinueCancelOrder}>Continue</Button>
          </DialogActions>
      </Dialog>

      { /*  Page loading indicator */}
      { (isOrderListLoading || isOrderLoading) && 
          <Backdrop
              sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
              open={true}
              onClick={() => {}}>
              <CircularProgress color="inherit" />
          </Backdrop>
      }
    </React.Fragment>
  );
}

export default OrderList;