import { GET_ALL_ORDER_REQUEST, GET_ALL_ORDER_SUCCESS, GET_ALL_ORDER_FAILED } from "./actionTypes";
import { GET_ORDER_REQUEST, GET_ORDER_SUCCESS, GET_ORDER_FAILED, CLEAR_SELECTED_ORDER } from "./actionTypes";
import { CANCEL_ORDER_REQUEST, CANCEL_ORDER_SUCCESS, CANCEL_ORDER_FAILED } from "./actionTypes";
import { ADD_ORDER_REQUEST, ADD_ORDER_SUCCESS, ADD_ORDER_FAILED, CLOSE_ADD_ORDER_POPUP } from "./actionTypes";

const initialState = {
    isOrderListLoading: false,
    isOrderLoading: false,
    isCancelLoading: false,
    orderList: [],
    error: "",
    selectedOrder: null,
    isAddLoading: false,
    isAddSuccess: false,
    isAddFailed: false,
};


const orderReducer = (state = initialState, action) => {
    switch (action.type) {
        // 1. GET ALL ORDERS
        case GET_ALL_ORDER_REQUEST: 
            return {
                ...state,
                isOrderListLoading: true,
            };

        case GET_ALL_ORDER_SUCCESS: 
            return {
                ...state,
                orderList: action.payload,
                error: "",
                isOrderListLoading: false,
            };

        case GET_ALL_ORDER_FAILED: 
            return {
                ...state,
                orderList: [],
                error: action.payload,
                isOrderListLoading: false,
            };


        // 2. GET SINGLE ORDER
        case GET_ORDER_REQUEST: 
            return {
                ...state,
                selectedOrder: null,
                isOrderLoading: true,
            };

        case GET_ORDER_SUCCESS: 
            return {
                ...state,
                selectedOrder: action.payload,
                error: "",
                isOrderLoading: false,
            };

        case GET_ORDER_FAILED: 
            return {
                ...state,
                selectedOrder: null,
                error: action.payload,
                isOrderLoading: false,
            };

        case CLEAR_SELECTED_ORDER: 
            return {
                ...state,
                selectedOrder: null,
                error: "",
                isOrderLoading: false,
            };

        // 3. CACNEL SELECTED ORDER
        case CANCEL_ORDER_REQUEST: 
            return {
                ...state,
                isCancelLoading: true,
            };

        case CANCEL_ORDER_SUCCESS: 
            return {
                ...state,
                error: "",
                isCancelLoading: false,
            };

        case CANCEL_ORDER_FAILED: 
            return {
                ...state,
                error: action.payload,
                isCancelLoading: false,
            };

        // 4. CREATE NEW ORDERS
        case ADD_ORDER_REQUEST: 
            return {
                ...state,
                isAddFailed: false,
                isAddSuccess: false,
                isAddLoading: true,
            };

        case ADD_ORDER_SUCCESS: 
            return {
                ...state,
                error: "",
                isAddLoading: false,
                isAddSuccess: true,
            };

        case ADD_ORDER_FAILED: 
            return {
                ...state,
                isAddLoading: false,
                isAddFailed: true,
            };

        case CLOSE_ADD_ORDER_POPUP: 
            return {
                ...state,
                isAddFailed: false,
                isAddSuccess: false,
                isAddLoading: false,
            };

        default:
            return state;

    }
}

export default orderReducer;