import AuthService from '../../services/auth.service';
import { STATUSCODE } from '../../utils/constants';
import StorageInstance from '../../utils/storage';

import { LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAILED } from "./actionTypes";
import { LOGOUT_REQUEST, LOGOUT_SUCCESS, LOGOUT_FAILED } from "./actionTypes";

const loginRequest = () => {
    return {
        type: LOGIN_REQUEST
    }
}

const loginSuccess = (userData) => {
    return {
        type: LOGIN_SUCCESS,
        payload: userData
    }
}

const loginFailed = (error) => {
    return {
        type: LOGIN_FAILED,
        payload: error
    }
}

const login = (username, password) => {
    return (dispatch) => {
        dispatch(loginRequest());
        AuthService.login(username, password)
            .then(response => {
                const data = JSON.parse(response.data);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    StorageInstance.addItem('userData', JSON.stringify(data.Results));
                    dispatch(loginSuccess(data.Results));
                } else {
                    dispatch(loginFailed(data.Results));
                }
            })
            .catch(error => {
                dispatch(loginFailed("Service Error..."));
            })
    }
}

const logoutRequest = () => {
    return {
        type: LOGOUT_REQUEST
    }
}

const logoutSuccess = () => {
    return {
        type: LOGOUT_SUCCESS
    }
}

const logoutFailed = (error) => {
    return {
        type: LOGOUT_FAILED,
        payload: error
    }
}

const logout = (loginId, sessionId) => {
    return (dispatch) => {
        dispatch(logoutRequest());
        AuthService.logout(loginId, sessionId)
            .then(response => {
                const data = JSON.parse(response.data);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    StorageInstance.removeItem('userData');
                    dispatch(logoutSuccess());
                } else {
                    dispatch(logoutFailed("Failed to logout"));
                }
            })
            .catch(error => {
                dispatch(loginFailed("Service Error..."));
            })
    }
}

export { login, logout }