import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import "./Header.scss";

const Header = ({ title, onRightBtnText, onRightBtnIcon, onRightBtnClick }) => {
    const onRightButtonClick = () => {
        onRightBtnClick();
    }

    return (
        <Paper className="mtx-paper-container">
            <Typography variant="overline" display="block" gutterBottom className='title'>
                {title}
            </Typography>
            <Button variant="outlined" startIcon={onRightBtnIcon} onClick={onRightButtonClick} >
                {onRightBtnText}
            </Button>
        </Paper>
    );

}

export { Header };