import { URL } from './url';
const axios = require('axios');

const createAxiosInstance = () => {
  const instance = axios.create({
    baseURL: URL.BASE_URL,
    timeout: 10000,
    transformResponse: [
      (data) => {
        try {
          return JSON.parse(data);
        } catch (e) {
          return data;
        }
      },
    ],
  });
  
  return instance;
};

const AxiosInstance = createAxiosInstance();

AxiosInstance.defaults.headers.common['ClientSignature'] = '216369402efd0789ec5eb33fddb0e54d814e2b6eade48743d300c8a7d553ba73';
AxiosInstance.defaults.headers.common['userid'] = 'ClientMobile';
// TODO: Not sure why its required.
AxiosInstance.defaults.headers.common['datetimestamp'] = '20200403115100';

// Request interceptor
axios.interceptors.request.use(function (config) {
  //config.headers.Authorization =  token;
  console.log('Axios request config:', config);
  return config;
});

export { AxiosInstance };

