import React, {useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import RemoveRedEyeRoundedIcon from '@mui/icons-material/RemoveRedEyeRounded';
import CloseIcon from '@mui/icons-material/Close';
import PrintRoundedIcon from '@mui/icons-material/PrintRounded';
import InfoIcon from '@mui/icons-material/Info';

import { getAllOrder } from './../../../../redux/order/actions';

import "./OrderTable.scss";

const OrderTable = ({onActionClick}) => {
    const dispatch = useDispatch();
    const userData = useSelector(state => state.auth.userData);
    const isOrderListLoading = useSelector(state => state.order.isOrderListLoading);
    const orderList = useSelector(state => state.order.orderList);

    console.log('orderList:', orderList);

    useEffect(() => {
        // Clear the input field if any error. 
        if (userData) {
            dispatch(getAllOrder(userData.LoginID, userData.Email, userData.RoleNum));
        }
    }, [userData]);

    const onOrderAction = (actionType, invoiceValue) => {
        onActionClick(actionType, invoiceValue);
    }

    return (
        <React.Fragment>
            { !isOrderListLoading ?
                orderList.length > 0 ? 
                    <TableContainer component={Paper} className="table-container">
                    <Table sx={{ minWidth: 650 }}>
                        <TableHead className="table-header">
                            <TableRow>
                            <TableCell>#ID</TableCell>
                            <TableCell align="right">Invoice ID</TableCell>
                            <TableCell align="right">Sender Phone</TableCell>
                            <TableCell align="right">Created On</TableCell>
                            {/* <TableCell align="right">Contact No</TableCell> */}
                            <TableCell align="right"></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody className="table-body">
                            {orderList.map((row, index) => (
                            <TableRow key={row.invoice_value} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                {/* <TableCell component="th" scope="row"> {row.id} </TableCell> */}
                                <TableCell > {index + 1} </TableCell>
                                <TableCell align="right">{row.invoice_value}</TableCell>
                                <TableCell align="right">{row.send_phone}</TableCell>
                                <TableCell align="right">{row.Entry_Date || ""}</TableCell>
                                {/* <TableCell align="right">{row.receiver.phone}</TableCell> */}
                                <TableCell align="right" style={{ padding: '5px'}} className={
                                    row.status === "CANCELLED" ? 'cursor-none'  :  'cursor-pointer'}>
                                    <RemoveRedEyeRoundedIcon className="action-button" onClick={() => { onOrderAction('VIEW_ORDER', row.invoice_value) }}/>
                                    {/* <EditRoundedIcon className="action-button" style={{ marginRight: '15px', color: '#0d6efd', cursor: 'pointer', width: '24px' }} 
                                    onClick={() => { console.log("Item " + row.id +" edit clicked") }} /> */}
                                    <PrintRoundedIcon className="action-button" onClick={() => { onOrderAction('PRINT_ORDER', row.invoice_value) }}/>
                                    <CloseIcon className="action-button delete" onClick={() => { onOrderAction('CANCEL_ORDER', row.invoice_value) }}/>
                                </TableCell>
                            </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>:
                <div className="order-list-empty-item">
                    <div className="content">
                        <InfoIcon /> There is no order, Please add new item.
                    </div>
                </div>
            : null
            }

        { /*  PAGINATION */}
        {/* <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Pagination count={3} variant="outlined" color="secondary" showFirstButton showLastButton
            style={{ marginTop: '15px' }} onChange={(event, pageNo) => { console.log('PageNo:', pageNo);}}/>
        </div> */}
        </React.Fragment>
    );

}

export { OrderTable };