import { LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAILED } from "./actionTypes";
import { LOGOUT_REQUEST, LOGOUT_SUCCESS, LOGOUT_FAILED } from "./actionTypes";
import StorageInstance from "../../utils/storage";

const defaultValue = {
    isLoading: false,
    userData: null,
    error: "",
    isLoggedIn: false
};

const userData = JSON.parse(StorageInstance.getItem('userData'));
const initialState = (userData && userData.Email !== null) 
    ? {...defaultValue, userData, isLoggedIn: true }
    : defaultValue;

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        // 1. LOGIN 
        case LOGIN_REQUEST: 
            return {
                ...state,
                isLoading: true,
            };

        case LOGIN_SUCCESS: 
            return {
                ...state,
                userData: action.payload,
                isLoggedIn: true,
                error: "",
                isLoading: false,
            };

        case LOGIN_FAILED: 
            return {
                ...state,
                userData: null,
                error: action.payload,
                isLoggedIn: false,
                isLoading: false,
            };

        // 2. LOGOUT 
        case LOGOUT_REQUEST: 
            return {
                ...state,
                isLoading: true,
            };

        case LOGOUT_SUCCESS: 
            return {
                ...state,
                userData: null,
                isLoggedIn: false,
                error: "",
                isLoading: false,
            };

        case LOGOUT_FAILED: 
            return {
                ...state,
                error: action.payload,
                isLoading: false,
            };

        default:
            return state;

    }
}

export default authReducer;