import React, { useState, useEffect } from 'react';
import { Outlet, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../../redux/auth/actions';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import Button from '@mui/material/Button';
import PowerSettingsNewIcon from '@mui/icons-material/PowerSettingsNew';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import './Layout.scss';

const Main = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [openLogoutPopup, setOpenLogoutPopup] = useState(false);
    const isLoading = useSelector(state => state.auth.isLoading);
    const userData = useSelector(state => state.auth.userData);
    const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
    const errorMessage = useSelector(state => state.auth.error);

    useEffect(() => {
        if (!isLoggedIn) {
            navigate("/");
        }

        if (errorMessage !== "") {
            // TODO: Need to display message
        }
    }, [isLoggedIn, errorMessage]);

    const handleLogout = async () => {
        handleClose();
        dispatch(logout(userData.LoginID, userData.Email));
    }

    const handleOpen = () => {
        setOpenLogoutPopup(true);
    }

    const handleClose = () => {
        setOpenLogoutPopup(false);
    }

    return (
        <div className="main-content">
           <div className="header">
               <PowerSettingsNewIcon className="logout-button" onClick={handleOpen}/>
           </div>
           <div className="outlet-content">
                <Outlet />
           </div>

           <Dialog
                open={openLogoutPopup}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description">
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Are you sure, you want to logout?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Cancel</Button>
                    <Button onClick={handleLogout} autoFocus variant="contained">OK</Button>
                </DialogActions>
            </Dialog>
            { isLoading && 
                <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={true}
                    onClick={() => {}}>
                    <CircularProgress color="inherit" />
                </Backdrop>
            }
        </div>
    );
};

export default Main;