import React from 'react';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { PrintDesign } from "./print-design/PrintDesign";
import "./OrderPrint.scss";

const OrderPrint = ({selectedOrder, onClose}) => {

    const onClosePopup = () => {
        onClose();
    }

    return (
        <React.Fragment>
            { /*  Order Preview */}
            <Dialog open={true} onClose={() => { onClosePopup() }} className="preview-window" >
                <DialogTitle style={{ display: 'flex', justifyContent: 'flex-end', padding: '30px' }}>
                <span style={{color: '#071f41', fontWeight: 600}}>INVOICE :</span> |||||||||||||||||||
                </DialogTitle>
                <DialogContent style={{ display: 'flex', justifyContent: 'center' }}>
                    <DialogContentText>
                    <PrintDesign 
                        senderDetails={selectedOrder?.sender} 
                        receiverDetails={selectedOrder?.receiver} 
                        itemDetails={selectedOrder?.itemList} 
                        isDocument={selectedOrder?.isDocument} 
                        selectedCurrency={selectedOrder?.currency} />
                    </DialogContentText>
                </DialogContent>
                <DialogActions style={{ padding: '30px' }}>
                    {/* <Button variant="contained" onClick={goToOrderList}>Go To Order List</Button>
                    <Button variant="contained" onClick={createNewOrder} autoFocus>Create New Order</Button> */}
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );

}

export { OrderPrint };