import React, {useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Button from '@mui/material/Button';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import { login } from '../../redux/auth/actions';
import './Login.scss';

function Login() {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [email,setEmail]= useState("");
    const [password,setPassword]=useState("");

    const isLoading = useSelector(state => state.auth.isLoading);
    const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
    // const userData = useSelector(state => state.userData);
    const errorMessage = useSelector(state => state.auth.error);

    useEffect(() => {
        // TODO:
        // 1. Show splash screen
        // 2. Check if the token is valid/expired and get new token
        console.log('isLoggedIn:', isLoggedIn);
        if (isLoggedIn) {
            navigate("/home");
        }
    }, [isLoggedIn]);

    useEffect(() => {
        // Clear the input field if any error. 
        if (errorMessage !== "") {
            setEmail("");
            setPassword("");
        }
    }, [errorMessage]);

    const authendicatelogin = () => {
        dispatch(login(email, password));
    }

    const gotoSignup = () => {
        navigate("/signup");
    }
    const gotoTrackShipment = () => {
        navigate("/track");
    }

    return(
        <div className="login-page">
            <div className="header">
                <Button color="warning" onClick={gotoTrackShipment}>Track Shipment</Button>
            </div>
            <div className="content">
                <div className="login-container">
                    <h1 className="login-title">Welcome to Matrix Logistics</h1>
                    <div className="">
                        <input type="text" placeholder="User ID" value={email} onChange={(e)=>setEmail(e.target.value)}
                            className="form-control"></input>
                        <br />
                        <input type="password" placeholder="Password" value={password} onChange={(e)=>setPassword(e.target.value)}
                            className="form-control"></input>

                        { errorMessage !== "" && 
                            <Box sx={{ color: 'error.main' }} className="error-box">{errorMessage}</Box>
                        }
                        <div className="action-button">
                            <button className="btn btn-primary" onClick={authendicatelogin} disabled={email === "" || password === ""} 
                                style={{width:"120px", margin:"20px 0px"}}>Login </button>
                        </div>
                        <div className="action-button">
                            <Link component="button" variant="body2" underline="hover" onClick={gotoSignup}> 
                                Register as Partner.
                            </Link>
                        </div>
                        
                    { isLoading && 
                        <Backdrop
                            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={true}
                            onClick={() => {}}>
                            <CircularProgress color="inherit" />
                        </Backdrop>
                    }
                    </div>
                </div>
            </div>
            {/* <div className="background-shape">
            </div> */}
        </div>
    );
}

export default Login;