import OrderService from '../../services/order.service';
import { STATUSCODE } from '../../utils/constants';
import { GET_ALL_ORDER_REQUEST, GET_ALL_ORDER_SUCCESS, GET_ALL_ORDER_FAILED } from "./actionTypes";
import { GET_ORDER_REQUEST, GET_ORDER_SUCCESS, GET_ORDER_FAILED, CLEAR_SELECTED_ORDER } from "./actionTypes";
import { CANCEL_ORDER_REQUEST, CANCEL_ORDER_SUCCESS, CANCEL_ORDER_FAILED } from "./actionTypes";
import { ADD_ORDER_REQUEST, ADD_ORDER_SUCCESS, ADD_ORDER_FAILED, CLOSE_ADD_ORDER_POPUP } from "./actionTypes";

import { getOrderResponseTransformer, addOrderRequestTransformer } from "./transform/transformer";

// 1. GET ALL ORDERS
const getAllOrderRequest = () => {
    return {
        type: GET_ALL_ORDER_REQUEST
    }
}

const getAllOrderSuccess = (orderList) => {
    return {
        type: GET_ALL_ORDER_SUCCESS,
        payload: orderList
    }
}

const getAllOrderFailed = (error) => {
    return {
        type: GET_ALL_ORDER_FAILED,
        payload: error
    }
}

const getAllOrder = (loginID, token, roleNum) => {
    return (dispatch) => {
        dispatch(getAllOrderRequest());
        OrderService.getAllOrder(loginID, token, roleNum)
            .then(response => {
                const data = JSON.parse(response.data);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    dispatch(getAllOrderSuccess(data.Results));
                } else {
                    dispatch(getAllOrderFailed(data.Results));
                }
            })
            .catch(error => {
                dispatch(getAllOrderFailed("Service Error..."));
            })
    }
}

// 2. GET SINGLE ORDER
const getOrderRequest = () => {
    return {
        type: GET_ORDER_REQUEST
    }
}

const getOrderSuccess = (selectedOrder) => {
    return {
        type: GET_ORDER_SUCCESS,
        payload: selectedOrder
    }
}

const getOrderFailed = (error) => {
    return {
        type: GET_ORDER_FAILED,
        payload: error
    }
}

const clearSelectedOrder = () => {
    return {
        type: CLEAR_SELECTED_ORDER
    }
}

const getOrder = (loginID, token, roleNum, invoiceId) => {
    return (dispatch) => {
        console.log("getOrder called...:")
        dispatch(getOrderRequest());
        OrderService.getOrder(loginID, token, roleNum, invoiceId)
            .then(response => {
                // const data = JSON.parse(response.data);
                // console.log("getOrder response:", data);
                // console.log("Transformers...:", orderResponseTransformer(response));
                const data = getOrderResponseTransformer(response);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    dispatch(getOrderSuccess(data.Results));
                } else {
                    dispatch(getOrderFailed(data.Results));
                }
            })
            .catch(error => {
                console.log("getOrder error response:");
                dispatch(getOrderFailed("Service Error..."));
            })
    }
}

// 3. CANCEL ORDER
const cancelOrderRequest = () => {
    return {
        type: CANCEL_ORDER_REQUEST
    }
}

const cancelOrderSuccess = () => {
    return {
        type: CANCEL_ORDER_SUCCESS
    }
}

const cancelOrderFailed = (error) => {
    return {
        type: CANCEL_ORDER_FAILED,
        payload: error
    }
}

const cancelOrder = (loginID, token, roleNum, invoiceId) => {
    return (dispatch) => {
        dispatch(cancelOrderRequest());
        OrderService.cancelOrder(loginID, token, roleNum, invoiceId)
            .then(response => {
                const data = JSON.parse(response.data);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    dispatch(cancelOrderSuccess());
                } else {
                    dispatch(cancelOrderFailed(data.Results));
                }
            })
            .catch(error => {
                dispatch(cancelOrderFailed("Service Error..."));
            })
    }
}

// 4. ADD ORDER
const addOrderRequest = () => {
    return {
        type: ADD_ORDER_REQUEST
    }
}

const addOrderSuccess = () => {
    return {
        type: ADD_ORDER_SUCCESS
    }
}

const addOrderFailed = (error) => {
    return {
        type: ADD_ORDER_FAILED,
        payload: error
    }
}
const closeAddOrderPopup = () => {
    return {
        type: CLOSE_ADD_ORDER_POPUP
    }
}

const addOrder = (loginID, token, roleNum, orderDetails) => {
    return (dispatch) => {
        dispatch(addOrderRequest());
        const requestBody = addOrderRequestTransformer(loginID, token, roleNum, orderDetails);
        // console.log('requestBody in action:', requestBody);
        OrderService.addOrder(requestBody)
            .then(response => {
                const data = JSON.parse(response.data);
                if (data && data.ResultCode === STATUSCODE.SUCCESS) {
                    dispatch(addOrderSuccess());
                } else {
                    dispatch(addOrderFailed());
                }
            })
            .catch(error => {
                dispatch(addOrderFailed("Service Error..."));
            })
    }
}

export { getAllOrder, getOrder, clearSelectedOrder, cancelOrder, addOrder, closeAddOrderPopup }